.footer{
    height: 230px;
    background-color: #F29A16;
    display: flex;
    align-items: center;
}

.container{
    margin: auto;
    width: 80%;
    display: flex;
    justify-content: space-between;
}

.container ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    font-family: 'Inter';
font-style: normal;
font-weight: 700;
padding: 0;
margin: 0;
font-size: 18px;
}

.container li{
    padding: 3px;
}

.container li:hover{
    cursor: pointer;
    text-decoration: underline;
}
.socialHandles{
    display: flex;
    align-items: flex-start;
}

.socialHandles img{
    margin-left: 18px;
}

.socialHandles img:hover{
    cursor: pointer;
}